import {HashRouter, Routes, Route, useNavigate} from 'react-router-dom';
import { useMutation} from "react-query";
import axios from "./utils/axios";
import { AddressesPage } from './pages/AddressesPage';
import { AuthPage } from './pages/AuthPage';
import { DiscountInfoPage } from './pages/DiscountInfoPage';
import { DiscountPage } from './pages/DiscountPage';
import { LoginPage } from './pages/LoginPage';
import { LoginVerificationPage } from './pages/LoginVerificationPage';
import { MainPage } from './pages/MainPage';
import { MyOrdersPage } from './pages/MyOrdersPage';
import { OrderPage } from './pages/OrderPage';
import { PaymentPage } from './pages/PaymentPage';
import { ProfilePage } from './pages/ProfilePage';
import { RegisterPage } from './pages/RegisterPage';
import { ServicesPage } from './pages/ServicesPage';
import { TipsPage } from './pages/TipsPage';
import Redirect from "./pages/Redirect";
import {ChangeOrderPage} from "./pages/ChangeOrderPage";
import {ReviewsPage} from "./pages/ReviewsPage";

import "./App.css"


function App() {
  const LoginUserName = async (code) => {
    const navigate = useNavigate()
    try {
      const { data } = await axios.post('auth-login/', code)
      //          
      if(data.token) {
        const token = data.token
        window.localStorage.setItem('VodoleyToken', token)
        const mytoken = window.localStorage.getItem('VodoleyToken')
        if(mytoken !== null) {
          navigate('/');
        }
      }
      return data
    } catch (error) {
      console.log(error)
    }
  }

  const user = useMutation(user => LoginUserName(user), {
    onSuccess: () => console.log('loged in')
  })

  const handleSubmit = data => {
    user.mutate(data)
  };


  return (
      <HashRouter>
        <main>
          <Routes>
            {/*<Route path='/' element={<Redirect/>}/> */}
            <Route path='/main' element={<MainPage />} /> {/*    */}
            <Route path='/address' element={<AddressesPage />} /> {/*   */}
            <Route path='/services' element={<ServicesPage showAddress={true} showButton={true} showHeader={true} />} /> {/*  */}
            <Route path='/discounts' element={<DiscountPage />} /> {/*   */}
            <Route path='/' element={<AuthPage />} /> {/*       */}
            <Route path='/info' element={<DiscountInfoPage />} /> {/*   */}
            <Route path='/login' element={<LoginPage />} /> {/*   */}
            <Route path='/login/verify' element={<LoginVerificationPage length={4} onSubmit={handleSubmit} />}/> {/*   */}
            <Route path='/register' element={<RegisterPage />} /> {/*   */}
            <Route path='/pay' element={<PaymentPage />} /> {/*   */}
            <Route path='/profile' element={<ProfilePage />} /> {/*  */}
            <Route path='/tips/:orderId' element={<TipsPage />} /> {/*  */}
            <Route path='/myorders' element={<MyOrdersPage />} /> {/*    */}
            <Route path='/makeorder' element={<OrderPage />} /> {/*   */}
            <Route path='/changeorder/:id' element={<ChangeOrderPage />} /> {/*   */}
            <Route path='/review/:id' element={<ReviewsPage />} /> {/*  */}
          </Routes>
        </main>
      </HashRouter>
  );
}

export default App;
